.main-search-container{
	display: grid;
	grid-template-columns: 1fr 1fr;
	
}

.spinner-container{
	grid-column: 1/3;
}

 @media only screen and (max-width: 985px) {
	 .main-search-container{
		 grid-template-columns: 1fr;
	 }

}
 .main-search-container{
	background: #fdfdfd;
}

.MuiCardHeader-title  {
	font-family: "Bellota Text"
}
.MuiCardHeader-title a{
	text-decoration: none;
}
.card-list-item-container{
	display: flex;

justify-content: space-evenly;
flex-wrap: wrap;
text-align: center;

align-items: center;
}
.card-list-item {
	display: inline;
	padding: 1% 1.5%;
	margin: 1%;
	font-size: 12px;
}

.search-item{
	margin: 2%;
}

.card-list-item_health {
color: white;
	background: #00c853;
}

.card-list-item_caution {
background: #ffeb3b;
color: black;

}

.card-list-item-total-nutrients-container{
	display: grid;
font-size: 14px;
	align-items: center;
grid-template-columns: repeat(auto-fill, minmax(125px, 1fr));
}

.card-list-item-total-nutrients-container > li{
	padding: 2%;
}

.MuiButtonBase-root > span{
	pointer-events: none;
	/* this is extrmely hacky fix later*/
}

.favorite-recipes{
	width: 100vh;
	height: 100vh;
	background: #f8f8f8;
}

.card-list-item-subtitle{
padding: 3% 0;
}


.calories-max-container {
	width: 100%;
	text-align: center;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.checkbox-subtitle {
	padding: 1%;
	font-weight: 600;
	
}

checkbox-item label{
	padding: 1%;
	
}

.calories-input {
	border: 0;
	/* padding: 1%; */
	font-size: 18px;
	padding: 1%;
	background: #ececec;
	border-bottom: 2px solid #3f51b5;
}

.checkboxes-container{
grid-template-columns:repeat(auto-fit, minmax(100px, 1fr));
		display:grid;
		padding:1% 5%;
}
.checkbox{
	position: absolute;
	opacity: 0;
	width: 50px;
height: 50px;
	
}

.checkbox-item{
	align-items: center;
	justify-content: center;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column
}

.search-form{
		padding: 2%;
justify-content: center;
	display:grid;
	align-items: center;
	justify-items:center;
	grid-template-columns: 1fr 1fr;
	background: #fdfdfd
}

.search-form label{
	opacity: 0.8;
	
}

.search-form-container {
/*	width: 85%; */
	padding: 3%;
	/* margin: 5%; */
	/* background: black; */
	height: 100%;
	/* align-self: center; */
	display: flex;
	flex-direction: column;
	justify-content: center;
}
	h1{
		margin: 0;
	}

.calories-input {
	border: 0;
	/* padding: 1%; */
	font-size: 18px;
	padding: 1%;
	background: white;
	/* border-bottom: 2px solid #3f51b5; */
	background: linear-gradient(120deg, #18b2fdb0, #1968ffc4);
	border-radius: 5px;
	background: 0;
	border-radius: 49px;
	padding: 1% 2%;
	background: #fff;
	/* color: black !important; */
	box-shadow: 0px 1px 3px #dbd8d8;
}
.main-search-title {
	margin: 2% 0px;
	font-weight: 600;
color: #0c76d2;
		font-family: "Bellota Text";
	font-size: 24px;
}

.form-parameters {
	width: 85%;
	background: #ffffffe3;
	padding: 1%;
	align-self: center;
	justify-self: center;
	background: #f9f9f9e3;
	padding: 2%;
	box-shadow: 0px 1px 1px 1px #d5d5d54f;
}

.checkbox-subtitle,.card-list-item-subtitle  {
	padding: 2% 0;
	font-weight: 600;
	color: #00c853;
}

.input-container{
	width: 100%;
	font-size: 32px;
	padding: 1%;
	/* border-radius: 26px; */
	border-bottom: 3px solid #2b6fc6;
	border: 0;
	background: #fff;
	background: #fff;
	/* border: 3px solid white; */
	border-radius: 30px;

	padding: 1% 2%;
	display: flex;

	flex-direction: row;

	justify-content: space-between;
	box-shadow: 0px 1px 3px #dbd8d8;
}
.query-input{
	border: 0;
}

.search-button {
	background: 0;
	border: 0;
}

