	.form-parameters{
		padding: 3%;
	}
	.checkboxs-container-super {
	padding: 2% 0;
}
	
	.search-form{
		grid-template-columns: 1fr ;
	}
}

@media only screen and (max-width: 500px){
	
	.input-container{
		font-size: 24px;
	}
}


